/* Start body rules */
body {
    background-image: linear-gradient(to top,#90B8B8 0%, #CED2D5 100%);
background-attachment: fixed;
  background-repeat: no-repeat;

    font-family: 'Vibur', cursive;
/*   the main font */
    font-family: 'Abel', sans-serif;
opacity: .95;
}

/* End body rules */

/* Start form  attributes */
.login_form {
    width: 450px;
    min-height: 500px;
    height: auto;
    border-radius: 5px;
    margin: 2% auto;
    box-shadow: 0 9px 50px hsla(20, 67%, 75%, 0.31);
    padding: 2%;
    background-image: linear-gradient(-225deg, #90B8B8 50%, #CED2D5 50%);
}

/* form Container */
form .con {
    display: flex;
    flex-direction: column;
    align-items: center;
    flex-wrap: wrap;
  
      margin: 0 auto;
}


.field-set
{
    display: flex;
    flex-direction: column;
    margin:10px;
    padding:10px;
    margin-top: 0px;
    padding-top:0px;
}

/* the header form form */
 header {
    padding:10px;
    margin:10px;
    text-align: center;
}
/* Login title form form */
 header h2 {
    font-size: 250%;
    font-family: 'Playfair Display', serif;
    color: #3e403f;
}
/*  A welcome message or an explanation of the login form */
header p {letter-spacing: 0.05em;}

.input-item {
    background: #fff;
    color: #333;
    padding:10px;
    margin:10px;

}

/* inputs form  */
form input[class="form-input"]{
    width: 240px;
    height: 30px;
    padding: 15px;
    font-size: 16px;
    font-family: 'Abel', sans-serif;
    color: #5E6472;
    outline: none;
    border: none;
    border-radius: 0px 5px 5px 0px;
    transition: 0.2s linear;
    
}
form input[id="txt-input"] {width: 250px;}
/* focus  */
input:focus {
    -webkit-transform: translateX(-2px);
            transform: translateX(-2px);
    border-radius: 5px;
}

.add_Profile_Picture
{
    display: flex;
    align-items: center;
    justify-content: center;
}

/* buttons  */
form button {
    display: inline-block;
    color: #252537;
  
    width: 280px;
    height: 50px;
  
    padding: 0 20px;
    background: #fff;
    border-radius: 5px;
    
    outline: none;
    border: none;
  
    cursor: pointer;
    text-align: center;
    transition: all 0.2s linear;
    
    margin: 7% auto;
    letter-spacing: 0.05em;
}

/*     Sign Up button  */
.sign-up {
    display: flex;
    justify-content: center;
    align-items: center;
    background: #B8F2E6;

    color: #252537;
    text-decoration: none;
    font-size: 14px;
  
    width: 280px;
    height: 50px;
  
    padding: 0 20px;
    border-radius: 5px;

  
    cursor: pointer;
    transition: all 0.2s linear;
    
    margin: 7% auto;
    margin-top: 10px;
    letter-spacing: 0.05em;
}


/* buttons hover */
.overlay button:hover {
    -webkit-transform: translatey(3px);
            transform: translatey(3px);
    box-shadow: none;
}

.overlay .sign-up:hover {
    -webkit-transform: translatey(3px);
            transform: translatey(3px);
    box-shadow: none;
}



/* buttons hover Animation */
.overlay button:hover {
    -webkit-animation: ani9 0.4s ease-in-out infinite alternate;
            animation: ani9 0.4s ease-in-out infinite alternate;
}
@-webkit-keyframes ani9 {
    0% {
        -webkit-transform: translateY(3px);
                transform: translateY(3px);
    }
    100% {
        -webkit-transform: translateY(5px);
                transform: translateY(5px);
    }
}
@keyframes ani9 {
    0% {
        -webkit-transform: translateY(3px);
                transform: translateY(3px);
    }
    100% {
        -webkit-transform: translateY(5px);
                transform: translateY(5px);
    }
}

.overlay .sign-up:hover {
    -webkit-animation: ani9 0.4s ease-in-out infinite alternate;
            animation: ani9 0.4s ease-in-out infinite alternate;
}
@keyframes ani9 {
    0% {
        -webkit-transform: translateY(3px);
                transform: translateY(3px);
    }
    100% {
        -webkit-transform: translateY(5px);
                transform: translateY(5px);
    }
}
